/************** MealDetails **************/

#mealDetailsPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  /* transition: bottom 250ms, opacity 250ms; */
}
#mealDetailsPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#mealDetailsPageBack {
  margin-left: 20px;
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}
#mealDetailsPageBack:hover {
  fill: black;
}
#mealDetailsPageHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
#mealDetailsKcal {
  margin: 15px 0;
  padding: 20px;
  color: var(--text-kinda-black);
  font-weight: bold;
  font-size: 40px;
  text-align: center;
  background-color: var(--background-white);
}
#mealDetailsKcal span {
  font-weight: normal;
  font-size: 20px;
  margin-right: 10px;
}
#mealDetailsInfoPercentContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
  background-color: var(--background-white);
}
#mealDetailsInfoPercentContainer div {
  width: 22%;
}
.mealDetailsPercentTitles {
  text-align: center;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
}
#mealDetailsNutritionInfoDetail {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  padding-bottom: 20px;
  margin-top: 15px;
  background-color: var(--background-white);
}
.mealDetailsNutritionInfoMain {
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
}
.mealDetailsNutritionInfoSub {
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  margin-top: 2px;
  margin-bottom: 2px;
  color: var(--text-kinda-black);
}
#mealDetailsNutritionInfoValues p {
  text-align: left;
}
@media (max-width: 450px) {
  #mealDetailsPageBack {
    width: 30px;
    height: 30px;
  }
}
